import React, { useState } from 'react';
import { timer } from 'rxjs';
import styled from 'styled-components';
import Display from './Display';
import { Button } from './Button';
import { debounce } from '../utils/debounce';

const Wrapper = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
`;

const Inner = styled.div`
  display: flex;
  margin-top: 25;
`;

const source$ = timer(0, 1000);
let subscriber;
const initialState = 0;

function App() {
  const [hours, setHours] = useState(initialState);
  const [minutes, setMinutes] = useState(initialState);
  const [seconds, setSeconds] = useState(initialState);
  const [disabled, setDisabled] = useState(false);

  const start = () => {
    subscriber = source$.subscribe((n) => {
      if (n > 59) {
        setMinutes((prevState) => prevState + 1);
        subscriber.unsubscribe();
        start();
      }
      setSeconds(n);
    });
  };

  if (minutes === 60) {
    setHours((prevState) => prevState + 1);
    setMinutes(initialState);
  }

  const setInitialTime = () => {
    setSeconds(initialState);
    setMinutes(initialState);
    setHours(initialState);
  };

  const unsubscribe = () => {
    if (subscriber) {
      subscriber.unsubscribe();
    }
  };

  const wait = () => {
    if (subscriber) {
      subscriber.isStopped = true;
    }
    setDisabled(false);
  };

  const stop = () => {
    unsubscribe();
    setInitialTime();
    setDisabled(false);
  };

  const reset = () => {
    if (!subscriber) {
      return;
    } else {
      unsubscribe();
    }
    setInitialTime();
    start();
  };

  return (
    <Wrapper>
      <Display time={{ seconds, minutes, hours }} />
      <Inner>
        <Button
          disabled={disabled}
          onClick={() => {
            setDisabled(true);
            start();
          }}
        >
          Start
        </Button>
        <Button onClick={stop}>Stop</Button>
        <Button className="wait" onClick={() => debounce(wait)}>
          Wait
        </Button>
        <Button onClick={reset}>Reset</Button>
      </Inner>
    </Wrapper>
  );
}

export default App;
